@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.logon-container {
  width: 100%;
  max-width: 1120px;
  height: 100vh;
  margin: 0 auto;

  padding: 10px;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logon-container section.form {
  width: 100%;
  max-width: 350px;
}

.logon-container section.form form {
  margin-top: 100px;
}

.logon-container section.form form h1 {
  font-size: 32px;
  margin-bottom: 32px;
}

@media (max-width: 950px) {
  .logon-container {
    flex-direction: column;
    padding: 32px 10px;
  }
  
  .logon-container section.form {
    max-width: 480px;
  }

  .logon-container section.form form {
    margin-top: 32px;
  }

  .logon-container img {
    max-width: 100%;
    padding: 10px;
  }
}
.register-container {
  width: 100%;
  max-width: 1120px;
  height: 100vh;
  margin: 0 auto;

  display: flex;
  align-items: center;
  justify-content: center;
}

.register-container .content {
  width: 100%;
  padding: 96px;
  background: var(--background);
  box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
  border-radius: 8px;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.register-container .content section {
  width: 100%;
  max-width: 380px;
}

.register-container .content section h1 {
  margin: 64px 0 32px;
  font-size: 32px;
}

.register-container .content section p {
  font-size: 18px;
  color: #737380;
  line-height: 32px;
}

.register-container .content form {
  width: 100%;
  max-width: 480px;
}

.register-container .content form input {
  margin-top: 8px;
}

.register-container .content form .input-group {
  display: flex;
}

.register-container .content form .input-group input + input {
  margin-left: 8px;
  text-align: center;
}

@media (max-width: 950px) {
  .register-container .content {
    padding: 32px 10px;
    flex-direction: column;
    box-shadow: none;
  }

  .register-container .content section {
    max-width: 480px;
    padding: 0 10px;
  }

  .register-container .content section img {
    margin-top: 64px;
  }

  .register-container .content section h1 {
    margin: 16px 0 8px;
  }

  .register-container .content section .back-link {
    padding: 0 0 5px 0;
  }
}
.profile-container {
  width: 100%;
  max-width: 1180px;
  padding: 0 30px;
  margin: 32px auto;
}

.profile-container header {
  display: flex;
  align-items: center;
}

.profile-container header span {
  font-size: 20px;
  margin-left: 24px;
}

.profile-container header img {
  height: 64px;
}

.profile-container header .button {
  width: 260px;
  margin-top: 0;
  margin-left: auto;
}

.profile-container header button {
  width: 60px;
  height: 60px;
  border-radius: 4px;
  border: 1px solid #dcdcd6;
  background: transparent;
  margin-left: 16px;
  transition: border-color 0.2s;
}

.profile-container header button:hover {
  border-color: #999;
}


.profile-container h1 {
  margin-top: 80px;
  margin-bottom: 24px;
}

.profile-container ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 24px;
  list-style: none;
}

.profile-container li {
  background: white;
  padding: 24px;
  border-radius: 8px;
  position: relative;
}

.profile-container li button {
  position: absolute;
  right: 24px;
  top: 24px;
  border: 0;
  background: transparent;
  transition: -webkit-filter 0.2s;
  transition: filter 0.2s;
  transition: filter 0.2s, -webkit-filter 0.2s;
}

.profile-container li button:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

.profile-container li strong {
  display: block;
  margin-bottom: 8px;
  color: #41414d;
}

.profile-container li p + strong {
  margin-top: 32px;
}

.profile-container p {
  color: #737380;
  line-height: 21px;
  font-size: 16px;
}

@media (max-width: 950px) {
  .profile-container {
    padding: 0 15px;
  }

  /*.profile-container header {
    margin-top: 200px;
  }
  .profile-container header img {
    position: absolute;
    top: 20px;
    left: 50%;
    width: 280px;
    height: 280px;
    margin-top: -80px;
    margin-left: -140px;
  }*/

  .profile-container header img {
    height: 64px;
    margin-right: 10px;
  }

  .profile-container header span {
    position: absolute;
    top: 120px;
    margin: 0;
    width: calc(100% - 30px);
    text-align: center;
    font-size: 22px;
  }

  .profile-container header .button {
    overflow-x: unset;
  }

  .profile-container header button {
    margin-left: 8px;
  }

  .profile-container h1 {
    margin-bottom: 16px;
  }

  .profile-container ul {
    grid-template-columns: repeat(1, 1fr);
  }
}
