/* Color Themes */
:root {
  --background: #f0f0f5;
  --foreground: #1d1d1d;
  --accent-color: #e02041;
}

:root[theme=dark] {
  --background: #1d1d1d;
  --foreground: #f0f0f5;
}

/* Global Styles */
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;

  background: #f0f0f5;

  background: var(--background);
  color: #1d1d1d;
  color: var(--foreground);

  overflow-y: auto;
}

input, button, textarea {
  font: 400 18px Roboto, sans-serif;
}

button {
  cursor: pointer;
}

form input, form textarea {
  width: 100%;
  height: 60px;
  color: #333;
  border: 1px solid #dcdce6;
  border-radius: 8px;
  padding: 0 24px;
}

form textarea {
  padding: 16px 24px;
  min-height: 140px;
  max-height: 400px;

  line-height: 24px;
  resize: vertical;
}

.button {
  width: 100%;
  height: 60px;
  background: #e02041;
  background: var(--accent-color);
  border: 0;
  border-radius: 8px;
  color: #fff;
  font-weight: bold;
  margin-top: 16px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  font-size: 18px;
  line-height: 60px;

  transition: -webkit-filter 0.2s;

  transition: filter 0.2s;

  transition: filter 0.2s, -webkit-filter 0.2s;
}

.button:hover {
  -webkit-filter:brightness(90%);
          filter:brightness(90%);
}

.back-link {
  display: flex;
  align-items: center;
  margin-top: 16px;
  padding: 19px 0;
  color: #41414d;
  font-size: 18px;
  text-decoration: none;
  font-weight: 500;
  transition: opacity 0.2s;
}

.back-link:hover {
  opacity: 0.8;
}

.back-link svg {
  margin-right: 8px;
}
